import React from "react";
import Head from "next/head";
import axios from "axios";
import { Fade, Table } from "reactstrap";

import Navbar from "../components/navbar";

import "../css/styles.scss";

class matchesTable extends React.Component {
  static async getInitialProps() {
    const api = "https://api.football-data.org/v2/competitions/PL/matches";
    const res = await axios.get(api, {
      headers: { "x-auth-token": `bb59bf677688476183511abee982ecd3` }
    });
    return {
      data: res, 
    }; 
  }

  constructor(props) {
    super(props); 
    
    this.state = {
      fadeIn: true, 
    }; 

    this.toggle = this.toggle.bind(this); 
  }

  toggle() {
    this.setState({
      fadeIn: false, 
    })
  }

  render() {
    console.log(this.data); 

    return(
      <div className="container-fluid main-container">
      <Head>
          <title> League Table </title>
          <meta
            name="viewport"
            content="initial-scale=1.0, width=device-width"
          />
          <link
            rel="stylesheet"
            href="https://stackpath.bootstrapcdn.com/bootstrap/4.3.1/css/bootstrap.min.css"
          />
          <link 
            rel="stylesheet"
            href="https://fonts.googleapis.com/css?family=Raleway" 
          />
          <link 
            rel="shortcut icon" 
            type="image/png" 
            href="https://github.com/Jzbonner/futbol-next/blob/master/img-media/soccer.png?raw=true"
          />
        </Head>

      <Navbar /> 
      <Fade in={this.state.fadeIn}>
      <div className="row main-content">
            <div className="col-sm-2" />
            <div className="col-sm-8">
              <h3> Match Table </h3>
              <Table light hover responsive className="table">
                <thead>
                  <tr className="table-row">
                    <th>Pos.</th>
                    <th>Team</th>
                    <th>Points</th>
                    <th>Games</th>
                    <th>Wins</th>
                    <th>Draws</th>
                    <th>Losses</th>
                  </tr>
                </thead>
              </Table>
            </div>
            <div className="col-md-2">
                <img 
                  className="league-logo"
                  src="https://github.com/Jzbonner/futbol-next/blob/master/img-media/premier-league.png?raw=true"
                  alt="league-logo"
                />
            </div>
          </div>
      </Fade>
      </div>
    )
  }
}

export default matchesTable; 